*{
  margin: 0;
  padding: 0;
}
::selection {
  color:  black;
  background: #09EAE1;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
    width: 10px;
    background: black;
}

/* Track */
::-webkit-scrollbar-track {
    /*box-shadow: inset 0 0 5px #09EAE1;*/
    /*border-radius: 1px;*/
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #09EAE1;
    border-radius: 10px;
}

:root{
    --poppins:'Poppins', sans-serif;
    --blue:#1da1f2;
}
html {
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
a {
    text-decoration: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    outline: none;
}


.navbar{
    position: fixed;
    top: 0;
    width: 100vw;
    height: 60px;
    display: flex;
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #414141 74%);
    align-items: center;
    justify-content: center;
    -webkit-filter:blur(1) ;
            filter:blur(1) ;
    z-index: 999;
}
.navbar__center{
    height: 100%;
    width: 70%;
    display: flex;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppins);
    background-color:transparent;
}
@media only screen and (max-width: 700px) {
    .navbar__center {
        height: 100%;
        width: 100%;
    }
}
.navbar .navbar__logo{
    padding-left: 2%;
    width: 60px;
    height: 60px;
    position: relative;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center center;
}
.navbar .navbar__logo img{
    width: 38px;
    border-radius: 50%;
    height: 38px;
    position: absolute;
    object-fit: cover;
    border: 2px solid #1da1f2;
    border: 2px solid var(--blue);
}
.navbar__head{
    flex: 1 1;
    height: 100%;
    display: flex;
    align-items: center;
    text-align: left;
    padding-left: 3%;
}
.navbar__head > p{
    font-size: 20px;
    font-weight: 600;
    color: #eeeeee;
}
.navbar__logout{
    height: 100%;
    width: 50px;
    margin-right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navbar__logout button{
    padding: 5px 10px;
    background-color: #09EAE1 ;
    /*color: white;*/
    font-weight: bold;
    border: none;
    color: black;
    outline: none;
    border-radius: 3px;
}
