    --poppins:'Poppins', sans-serif;
    --blue:#1da1f2;
    --prompt: 'Prompt', sans-serif;
}
html {
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
a,button{
