body{
    background-color: #000000;
    height: 100%;
}

.news{
    width: 100%;
    /*height: 150px;*/
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppins);
}
.news .news__body{
    padding-top: 15px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.news .news__body .content{
    display: flex;
    align-items: center;
    max-width: 500px;
    /*height: 500px;*/
    border: 1px solid #09EAE1;
    flex-direction: column;
    padding: 5px;
    margin-bottom: 10px;
}
.news__left__top{
    width: 100%;
    flex: .3 1;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    background-color: #fff;
}
.news__left__top .news__image img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: relative;
    display: flex;
    align-items: center;

}
.link{
    position: absolute;
    left: 0;
    bottom: 0%;
    padding: 5px;
    background-color: #09EAE1;
    border-radius: 5px;
}
.bookmark{
    position: absolute;
    right: 0;
    bottom: 0%;
    padding: 5px;
    color: #09EAE1;
}
.bookmark button{
    outline: none;
    border: none;
    background-color: transparent;
    height: 20px;
    width: 20px;
}
.bookmark button i{
    /*color: #09EAE1;*/
    font-size: 20px;
    color: springgreen;
    cursor: pointer;
}
.link a{
    color: black;
    font-weight: bold;
    font-size: 16px;
    outline: none;
    border: none;
}
.news__right__bottom{
    width: 100%;
    padding: 5px;
    flex: .3 1;
}
.news__right__bottom p{
    padding: 5px 0px;
    font-size: 12px;
    line-height: 24px;
    color: #09EAE1;
}
.news__offline{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.news__offline .news__offline__image{
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.news__offline .news__offline__image img {
    height: 40px;
    width: 40px;
    object-fit: contain;
}
.news__offline .news__offline__text{
    color: gray;
    padding: 8px;
}

.newsfeed{
    margin-top: 60px;
}
